
























































import { Component, Vue } from 'vue-property-decorator'
import { namespace, State } from 'vuex-class'
import { IntegerPlusminus } from 'vue-integer-plusminus'
import interact from 'interactjs'
const spi = namespace('spirit')

@Component({
  components: { IntegerPlusminus }
})
export default class spirit extends Vue {
  @State spirit
  @spi.Mutation setPoint
  @spi.Mutation spawn
  @spi.Mutation void
  movePos = ''
  moveIndex = -2
  moveType = ''
  dx = 0
  dy = 0

  selected(pos, index) {
    return this.movePos == pos && this.moveIndex == index
  }

  mounted() {
    interact('.slider')
      .draggable({
        onstart: ({ currentTarget }) => {
          this.movePos = currentTarget.dataset.pos
          this.moveIndex = currentTarget.dataset.index
        },
        onmove: ({ dx, dy }) => {
          dx *= devicePixelRatio
          dy *= devicePixelRatio
          this.dx += dx
          if (
            this.movePos == 'foe' &&
            matchMedia('(max-aspect-ratio: 1/1)').matches
          ) {
            this.dy += dy
          } else {
            this.dy -= dy
          }
          if (this.dx < this.dy) {
            if (this.dy < 0) {
              this.moveType = ''
            } else if (this.dy < 100) {
              this.moveType = 'earth'
            } else if (100 < this.dy && this.dy < 200) {
              this.moveType = 'fire'
            } else if (200 < this.dy && this.dy < 300) {
              this.moveType = 'water'
            } else if (300 < this.dy && this.dy < 400) {
              this.moveType = 'wood'
            } else {
              this.moveType = 'metal'
            }
          } else if (0 < this.dx) {
            this.moveType = ''
          }
        },
        onend: () => {
          if (0 < this.dy && this.dx < this.dy) {
            this.spawn({
              pos: this.movePos,
              index: this.moveIndex,
              type: this.moveType
            })
          } else if (100 < this.dx) {
            this.void()
          }
          this.movePos = ''
          this.moveIndex = -1
          this.moveType = ''
          this.dy = this.dx = 0
        }
      })
      .on('tap', ({ currentTarget }) => {
        this.setPoint({
          pos: currentTarget.dataset.pos,
          index: currentTarget.dataset.index,
          payload: 0
        })
      })
  }
  beforeDestroy() {
    interact('.slider').unset()
  }
}
